import { Component, OnInit, Input, EventEmitter, Output } from '@angular/core';
import { Pelicula } from 'src/clases/Pelicula';
import { MiservicioService } from 'src/app/servicios/miservicio.service';

@Component({
  selector: 'app-tabla',
  templateUrl: './tabla.component.html',
  styleUrls: ['./tabla.component.css']
})
export class TablaComponent implements OnInit {
  @Input() listado: Pelicula[];
  @Output() enviarPelicula: EventEmitter<any> = new EventEmitter<any>();
  private pelicula: Pelicula;

  constructor(private servico: MiservicioService) { }

  ngOnInit(): void {
    this.servico.GetPeliculas();
  }

  seleccionarPelicula(id: number)
  {
    console.info(id);
    this.pelicula = JSON.parse(localStorage.getItem('pelicula')).find( data => data.id == id);
  }
}
